const num = 10;

const generateMulti = (num) => {
    const first = Math.floor(Math.random() * (num - 1)) + 1;
    const second = Math.floor(Math.random() * (num - 1)) + 1;
    const result = first * second;
    const operation = "х";
    return {first, operation, second, result, uid: Math.random()};
}

const generator = () => {
    return [
        generateMulti(num),
        generateMulti(num),
        generateMulti(num),
        generateMulti(num)
    ]
}

export default generator;


