import React from 'react';
import Template from '../MathExprChallengePageTemplate';

const settings = {
    title: 'Умножение на 5',
    description: 'Вычислите примеры'
}

const generator = () => {
    const base = 5;
    const num = 10;
    const genExpression = (num) => {
        const first = base;
        const second = Math.floor(Math.random() * (num - 1)) + 1;
        const result = first * second;
        const operation = "х";
        return {first, operation, second, result, uid: Math.random()};
    }
    return [
        genExpression(num),
        genExpression(num),
        genExpression(num),
        genExpression(num)
    ];
}

const Multi5 = (props) => <Template settings={settings} generator={generator}/>;

export default Multi5;


