import React, { Component } from 'react';
import './App.css';
import { Route, Switch, withRouter } from 'react-router-dom';

import Component404 from './components/404';
import Login from './components/Login';
import ChallengeList from './components/ChallengeList';
import NumSumSubChallenge from './components/NumSumSubChallenge';

import SumSubNumberChallenge from './components/SumSubNumberChallenge/index';

import Multi10 from './components/Multi-10';
import Multi2 from './components/Multi-2';
import Multi3 from './components/Multi-3';
import Multi4 from './components/Multi-4';
import Multi5 from './components/Multi-5';


import Page from './components/Page';

import { AppProvider } from './context';

const App = (props) => {
  return (
    <AppProvider>
      <Switch>
        <Route exact path="/" render={props => <Login {...props} />} />
        <Route exact path="/challenge-list" render={props => <ChallengeList {...props} />} />
        <Route exact path="/num-10-sum-sub" render={props => <NumSumSubChallenge maxNumber={10} {...props} />} />
        <Route exact path="/num-20-sum-sub" render={props => <NumSumSubChallenge maxNumber={20} {...props} />} />
        {/* <Route exact path="/num-100-sum-sub" render={props => <NumSumSubChallenge maxNumber={100} {...props} />} /> */}
        <Route exact path="/num-100-sum-sub" render={props => <SumSubNumberChallenge {...props} />} />
        <Route exact path="/multi-10" render={props => <Multi10 {...props}/> }/>
        <Route exact path="/multi-2" render={props => <Multi2 {...props}/> }/>
        <Route exact path="/multi-3" render={props => <Multi3 {...props}/> }/>
        <Route exact path="/multi-4" render={props => <Multi4 {...props}/> }/>
        <Route exact path="/multi-5" render={props => <Multi5 {...props}/> }/>
        <Route render={props => <Page MainComponent={() => <h1>Страница в разработке</h1>} {...props}/>} />
      </Switch>
    </AppProvider>
  );
}

export default withRouter(App);



