const generateSumExpression = (limit) => {
    const first = Math.floor(Math.random() * limit);
    const second = Math.floor(Math.random() * limit);
    const result = first + second;
    const operation = "+";
    return { first, second, result, operation, uid: Math.random() };
};

const generateSubExpression = (limit) => {
    const operation = "-";
    while (true) {
        let first = Math.floor(Math.random() * limit);
        let second = Math.floor(Math.random() * limit);
        let result = first - second;
        if (result >= 0) {
            return { first, second, result, operation, uid: Math.random() };
        }
    }
}

const generateMathExpresstions = (limit) => {
    return [
        generateSumExpression(limit),
        generateSumExpression(limit),
        generateSubExpression(limit),
        generateSubExpression(limit)
    ];
}

export default generateMathExpresstions;


