import React from 'react';

export const AppContext = React.createContext();

export class AppProvider extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            name: null,
            score: 0
        }
    }

    render() {
        return (
            <AppContext.Provider
                value={{
                    state: this.state,
                    setName: name => this.setState({ ...this.state, name }),
                    setScore: score => this.setState({...this.state, score})
                }}
            >
                {this.props.children}
            </AppContext.Provider>
        );
    }
}


