import React from 'react';
import Template from '../MathExprChallengePageTemplate';
import generator from './generator';
import settings from './settings.json';

const Multi10 = (props) => {
    return (
        <Template settings={settings} generator={generator} {...props}/>
    );
}

export default Multi10;


