import React from 'react';
import { ChallengeContext } from '../../context/ChallengeContext';
import Card from './Card';

const ChallengeComponent = ({DataComponent, generator}) => {

    const challengeContext = React.useContext(ChallengeContext);

    if (!challengeContext.state.data.length) {
        const data = generator();
        challengeContext.setNewState({ ...challengeContext.state, data });
    }

    const [isChecked, setIsChecked] = React.useState(false);

    const handleNextClick = e => {
        e.preventDefault();
        if (challengeContext.state.current >= challengeContext.state.count) {
            challengeContext.setStatus('RESULT');
        } else {
            const current = challengeContext.state.current + 1;
            const data = generator();
            challengeContext.setNewState({ ...challengeContext, current, data });
            setIsChecked(false);
        }
    }

    const handleCheckClick = e => {
        e.preventDefault();
        let rightExpr = 0;
        const exprs = document.querySelectorAll('.math-expr');
        [...exprs].forEach((expr, i) => {
            let input = expr.querySelector('.result-input');
            const value = input.valueAsNumber;
            input.setAttribute('readonly', true);
            if (value === challengeContext.state.data[i].result) {
                input.classList.add('result-right');
                rightExpr++;
            }
            else {
                input.classList.add('result-wrong');
                expr.querySelector('.result')
                    .classList
                    .replace('result-hide', 'result-show');
            }
        });
        const turnScore = rightExpr === 4 ? 10 : rightExpr === 3 ? 2 : 0;
        const totalScore = challengeContext.state.score + turnScore;
        challengeContext.setNewState({ ...challengeContext.state, score: totalScore });
        setIsChecked(true);
    }

    return (
        <React.Fragment>
            <Card>
                {
                    challengeContext.state.data.map(data => <DataComponent key={data.uid} {...data} />)
                }
            </Card>
            {
                !isChecked ?
                    <button className="btn btn-success d-block mx-auto mt-4" onClick={handleCheckClick}>Проверить</button>
                    :
                    <button className="btn btn-success d-block mx-auto mt-4" onClick={handleNextClick}>Далее</button>
            }

        </React.Fragment>
    );
}

export default ChallengeComponent;


