import React from 'react';
import { ChallengeContext } from '../context/ChallengeContext';

const Challenge = ({ BeginComponent, ChallengeComponent, ResultComponent, ...props }) => {
    const challengeContext = React.useContext(ChallengeContext);
    switch (challengeContext.state.status) {
        case 'BEGIN': return <ChallengeComponentLayout WrappedComponent={BeginComponent}/>;
        case 'CHALLENGE': return <ChallengeComponentLayout WrappedComponent={ChallengeComponent}/>;
        case 'RESULT': return <ChallengeComponentLayout WrappedComponent={ResultComponent}/>;
        default: return <ChallengeComponentLayout WrappedComponent={BeginComponent}/>;
    }
}

const ChallengeComponentLayout = ({ WrappedComponent, ...props }) => {
    return (
        <main className="container">
            <div className="col-12 col-lg-6 col-md-8 col-sm-10 mx-auto mt-4">
                <WrappedComponent {...props} />
            </div>
        </main>
    );
}

export default Challenge;


