import React from 'react';
import Page from './Page';

import {ChallengeProvider} from '../context/ChallengeContext';

const ChallengePage = ({Challenge, ...props}) => {
    return (
        <ChallengeProvider>
            <Page MainComponent={Challenge} {...props}/>
        </ChallengeProvider>
    );
}

export default ChallengePage;


