import React from 'react';
import { ChallengeContext } from '../../context/ChallengeContext';
import Card from './Card';

const BeginComponent = (props) => {
    const challengeContext = React.useContext(ChallengeContext);
    const handlerBeginClick = e => {
        e.preventDefault();
        challengeContext.setStatus('CHALLENGE');
    }
    return (
        <React.Fragment>
            <Card>
                <h5>Title</h5>
                <p>Begin description</p>
            </Card>
            <button className="btn btn-success d-block mx-auto mt-4" onClick={handlerBeginClick}>Начать</button>
        </React.Fragment>
    );
}

export default BeginComponent;


