import React from 'react';

const DataComponent = ({first, operation, second, result}) => {
    return (
        <div className='math-expr'>
            <div className="">{first}</div>
            <div className="">{operation}</div>
            <div className="">{second}</div>
            <div className="">=</div>
            <input className="result-input" type="number" autoComplete="off" max="999" maxLength="3" />
            <div className="result result-hide">{result}</div>
        </div>
    );
}

export default DataComponent;


