import React from 'react';

import { AppContext } from '../context';

const Login = props => {

    // HOOKS
    const [isNameValid, setIsNameValid] = React.useState(true);
    const context = React.useContext(AppContext);

    // Ref to name input
    let inputName = null;

    const handlerOnClickNextButton = e => {
        e.preventDefault();

        if (inputName.value && inputName.value.trim().length > 0) {
            context.setName(inputName.value);
            props.history.push('/challenge-list');
        }
        else {
            setIsNameValid(false);
        }
    }

    return (
        <main className="container login-container">
            <section className="col-12 col-lg-4 login-section">
                <input type="text"
                    className="form-control mb-2 login-input"
                    id="login-input"
                    placeholder="Введите ваше имя"
                    maxLength="16"
                    autoComplete="off"
                    ref={r => inputName = r}
                    onFocus={e => setIsNameValid(true)}
                />
                {
                    !isNameValid && <small className="form-text text-danger">Не корректное имя</small>
                }
                <button className="btn btn-block btn-primary btn-login" onClick={handlerOnClickNextButton}>
                    Продолжить
                </button>
            </section>
        </main>
    );
};

export default Login;


