import React from 'react';
import Header from './Header';

const NumSumSubChallenge = (props) => {

    const { maxNumber } = props;

    const count = 10;
    const current = 1;
    const isChecked = false;
    const score = 0;
    const isFinish = false;

    const [challengeState, setChallengeState] = React.useState({ current, count, isChecked, isFinish, score });
    const [mathExpessions, setMathExpressions] = React.useState(generateMathExpresstions(maxNumber));

    const handleCheckClick = e => {
        e.preventDefault();
        let rightExpr = 0;
        const exprs = document.querySelectorAll('.math-expr');
        [...exprs].forEach((expr, i) => {
            let input = expr.querySelector('.math-expr-input');
            const value = input.valueAsNumber;
            input.setAttribute('readonly', true);
            if (value === mathExpessions[i].result) {
                input.classList.add('math-expr-right');
                rightExpr++;
            }
            else {
                input.classList.add('math-expr-wrong');
                expr.querySelector('.math-expr-result')
                    .classList
                    .replace('math-expr-result-hide', 'math-expr-result-show');
            }
        });
        const turnScore = rightExpr === 4 ? 10 : rightExpr === 3 ? 2 : 0;
        const totalScore = challengeState.score + turnScore;
        setChallengeState({ ...challengeState, isChecked: true, score: totalScore });
    }

    const handleNextClick = e => {
        e.preventDefault();
        if (challengeState.current >= challengeState.count) {
            setChallengeState({ ...challengeState, isFinish: true })
        }
        else {
            setChallengeState({ ...challengeState, current: challengeState.current + 1, isChecked: false });
            setMathExpressions(generateMathExpresstions(maxNumber));
        }
    }

    return (
        <React.Fragment>
            <Header />
            <main className="container">
                <h1 className="text-warning text-center">Числа до {maxNumber}: сложение и вычитание</h1>
                <div className="col-12 col-lg-6 col-md-8 col-sm-10 mx-auto">
                    {
                        !challengeState.isFinish ? (
                            <React.Fragment>
                                <ChallengeCard
                                    current={challengeState.current}
                                    count={challengeState.count}
                                    score={challengeState.score}
                                    mathExprs={mathExpessions}
                                    MathExprs={MathExpression} />
                                {
                                    !challengeState.isChecked ?
                                        <button className="btn btn-success d-block mx-auto mt-4" onClick={handleCheckClick}>Проверить</button>
                                        :
                                        <button className="btn btn-success d-block mx-auto mt-4" onClick={handleNextClick}>Далее</button>
                                }
                            </React.Fragment>
                        ) :
                        <ChallengeResult current={challengeState.current} count={challengeState.count} score={challengeState.score} />
                    }
                </div>
            </main>
        </React.Fragment>
    );
}

const ChallengeCard = ({ current, count, score, mathExprs, MathExprs }) => {
    return (
        <div className="card">
            <div className="card-header d-flex">
                <div className="w-100 text-left">
                    <span>{current} / {count}</span>
                </div>
                <div className="w-100 text-right">
                    <span>Очки: <span>{score}</span></span>
                </div>
            </div>
            <div className="card-body">
                <div className="d-flex flex-column">
                    {
                        mathExprs.map(({ uid, ...m }) => <MathExprs {...m} key={uid} />)
                    }
                </div>
            </div>
        </div>
    );
}

const ChallengeResult = ({ current, count, score }) => {
    return (
        <div className="card">
            <div className="card-header d-flex">
                <div className="w-100 text-left">
                    <span>{current} / {count}</span>
                </div>
                <div className="w-100 text-right">
                    <span>Очки: <span>{score}</span></span>
                </div>
            </div>
            <div className="card-body">
                <p className="font-weight-bolder">Вы заработали: <span>{score} {score % 10 === 1 ? 'очко' : score > 1 && score < 5 ? 'очка' : 'очков'}</span></p>
            </div>
        </div>
    );
}

const MathExpression = ({ first, second, result, operation }) => {
    return (
        <div className="math-expr">
            <div className="math-expr-first">{first}</div>
            <div className="math-expr-operstion">{operation}</div>
            <div className="math-expr-second">{second}</div>
            <div className="math-expr-equal">=</div>
            <input className="math-expr-input" type="number" autoComplete="off" max="999" maxLength="3" />
            <div className="math-expr-result math-expr-result-hide">{result}</div>
        </div>
    );
}

const generateMathExpresstions = (limit) => {
    return [
        generateSumExpression(limit),
        generateSumExpression(limit),
        generateSubExpression(limit),
        generateSubExpression(limit)
    ];
}

const generateSumExpression = (limit) => {
    const first = Math.floor(Math.random() * limit);
    const second = Math.floor(Math.random() * limit);
    const result = first + second;
    const operation = "+";
    return { first, second, result, operation, uid: Math.random() };
};

const generateSubExpression = (limit) => {
    const operation = "-";
    while (true) {
        let first = Math.floor(Math.random() * limit);
        let second = Math.floor(Math.random() * limit);
        let result = first - second;
        if (result >= 0) {
            return { first, second, result, operation, uid: Math.random() };
        }
    }
}

export default NumSumSubChallenge;


