import React from 'react';

const ChallengesList = props => {
    const { title, text, path } = props;
    const handleOnClickBeginButton = e => {
        e.preventDefault();
        props.history.push(path);
    }
    return (
        <div className="col-12 col-lg-4 col-md-6 col-sm-6 my-2">
            <div className="card challenge-card">
                <div className="card-body challenge-card-body">
                    <h5 className="card-title">{title}</h5>
                    <p className="card-text">{text}</p>
                    <a href="#" className="btn btn-primary" onClick={handleOnClickBeginButton}>Начать</a>
                </div>
            </div>
        </div>
    );
}

export default ChallengesList;


