import React from 'react';
import Card from './Card';

import {ChallengeContext} from '../../context/ChallengeContext';

const ResultComponent = (props) => {
    const challengeContext = React.useContext(ChallengeContext);
    const {score, time} = challengeContext.state;
    return (
        <React.Fragment>
            <Card>
                <p>Вы заработали: {score} {score % 10 === 1 ? 'очко' : score > 1 && score < 5 ? 'очка' : 'очков'}</p>
                <p>Время: {time}</p>
            </Card>
        </React.Fragment>
    );
}

export default ResultComponent;


