import React from 'react';
import BeginComponent from './BeginComponent';
import ChallengeComponent from './ChallengeComponent';
import ResultComponent from './ResultComponent';
import DataComponent from './DataComponent';
//import generator from './generator';

import Challenge from '../Challenge';
import ChallengePage from '../ChallengePage';

const MathExprChallengePageTemplate = ({settings, generator, ...props}) => {
    return (
        <ChallengePage Challenge={() =>
            <Challenge
                BeginComponent={() => <BeginComponent {...settings}/>}
                ChallengeComponent={() => <ChallengeComponent DataComponent={DataComponent} generator={generator} />}
                ResultComponent={() => <ResultComponent {...settings}/>}
                {...props}
            />}
        />
    );
}

export default MathExprChallengePageTemplate;




