import React from 'react';

export const ChallengeContext = React.createContext();

export class ChallengeProvider extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            status: 'BEGIN',
            count: 10,
            current: 1,
            score: 0,
            time: 0,
            data: []            
        }
    }

    render() {
        return (
            <ChallengeContext.Provider value={{
                state: this.state,
                setNewState: state => this.setState({...this.state, ...state}),
                setStatus: status => this.setState({...this.state, status}),
                setTime: time => this.setState({...this.state, time})
            }}>
                {this.props.children}
            </ChallengeContext.Provider>
        );
    }
}


